/* Global Styles */
@import url('https://fonts.googleapis.com/css2?family=Figtree:wght@300;400;600;700&display=swap');

:root {
  /* Colors */
  --dark-bg: #121212; /* Background like this site */
  --grid-bg: #1e1e1e; /* Slightly lighter dark for the grid */
  --grid-line: #444444; /* Subtle gray for grid lines */
  --timeline-past: #808080; /* Gray for past timeline */
  --timeline-future: #00bcd4; /* Cyan, glowing effect */
  --text-color: #ffffff; /* White text */
  --button-bg: #2a2a2a; /* Modern rounded button background */
  --button-hover: #3c3c3c; /* Slightly brighter gray on hover */
  --present-circle: #f28d8d; /* Highlight color for the present time */
  --border: #444444; /* Border around the screen */
  --shadow-color: rgba(0, 188, 212, 0.5); /* Glow effect */

  /* Font */
  --font-family: 'Figtree', sans-serif;

  /* General Spacing */
  --border-radius: 12px;
}

body {
  margin: 0;
  font-family: var(--font-family);
  background-color: var(--dark-bg);
  color: var(--text-color);
  overflow: hidden; /* No scrollbars */
}

.App {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Top Menu Bar */
.navbar {
  background-color: var(--grid-bg);
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 2px 4px var(--shadow-color);
}

.navbar-item {
  color: var(--text-color);
  font-weight: bold;
  text-decoration: none;
  margin: 0 10px;
}

.navbar-item:hover {
  color: var(--timeline-future);
}

.app-title {
  font-size: 1.5rem;
  font-weight: bold;
}

/* Hero Section */
.hero {
  background-color: var(--grid-bg);
  color: var(--text-color);
  text-align: center;
  padding: 50px 20px;
}

.hero .title {
  font-size: 3rem;
  font-weight: bold;
}

.hero .subtitle {
  font-size: 1.5rem;
  margin-bottom: 20px;
}

.hero .button {
  background-color: var(--button-bg);
  color: var(--text-color);
  font-weight: bold;
  padding: 10px 20px;
  border-radius: var(--border-radius);
  transition: background-color 0.3s ease;
  border: none;
}

.hero .button:hover {
  background-color: var(--button-hover);
}

/* Timeline Wrapper */
.timeline-wrapper {
  width: 100%;
  height: calc(100vh - 60px); /* Adjust for menu height */
  overflow: hidden;
  position: relative;
  cursor: grab;
}

.timeline-wrapper:active {
  cursor: grabbing;
}

/* Timeline Line */
.timeline-line {
  position: absolute;
  top: 50%;
  left: 0;
  height: 2px;
  background-color: var(--cyan-button);
  transform: translateY(-50%);
  z-index: 1;
  box-shadow: 0 0 10px var(--cyan-button);
  width: 100%;
}

.present-point {
  position: absolute;
  top: 50%;
  left: 0;
  width: 10px;
  height: 10px;
  background-color: white;
  border-radius: 50%;
  transform: translateY(-50%);
  z-index: 2;
}

/* Grid Cells */
.grid-cell {
  position: absolute;
  width: 50px;
  height: 50px;
  background-color: rgba(255, 255, 255, 0.2);
  border: 1px solid var(--gray-line);
  border-radius: 5px;
}

.grid-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

/* Floating Add Event Button */
.add-event-button {
  position: fixed;
  bottom: 20px;
  right: 20px;
  background-color: var(--button-bg);
  color: var(--text-color);
  padding: 15px;
  font-size: 18px;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 0 4px 6px var(--shadow-color);
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
  z-index: 4;
}

.add-event-button:hover {
  background-color: var(--button-hover);
  box-shadow: 0 6px 10px var(--shadow-color);
}

/* Responsive */
@media (max-width: 768px) {
  .hero .title {
    font-size: 2rem;
  }
  .hero .subtitle {
    font-size: 1.2rem;
  }
}
